import React from 'react';
import { Box, Tabs, Tab, Grid, CardMedia, Typography } from '@mui/material';

export default function Gallery() {
  const [tab, setTab] = React.useState(0);
  
  return (
    <Box>
      <Typography variant="h4" gutterBottom>Gallery</Typography>
      <Tabs value={tab} onChange={(_, v) => setTab(v)}>
        <Tab label="Photos" />
        <Tab label="Videos" />
      </Tabs>

      {tab === 0 && (
        <Grid container spacing={2} sx={{ mt: 2 }}>
          {[1,2,3,4,5,6].map(i => (
            <Grid item xs={6} sm={4} md={3} key={i}>
              <Box
                sx={{
                  width: '100%',
                  height: 200,
                  bgcolor: '#e0e0e0',
                  borderRadius: 1,
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  fontSize: '0.875rem'
                }}
              >
                Gallery Image {i}
              </Box>
            </Grid>
          ))}
        </Grid>
      )}

      {tab === 1 && (
        <Box sx={{ mt: 2 }}>
          <Typography variant="body1" sx={{ mb: 2 }}>Videos will appear here. Embed YouTube videos as needed.</Typography>
          <Box
            sx={{
              width: '100%',
              height: 360,
              bgcolor: '#e0e0e0',
              borderRadius: 1,
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center'
            }}
          >
            Video Player
          </Box>
        </Box>
      )}
    </Box>
  );
}
